<template>
  <div class="stu-info">
    <div class="stu-box">
      <myForm :myForm="studentForm" :postList="postList" />
      <div class="table-list">
        <el-divider />
        <myTable
          ref="myTable"
          v-loading="loading"
          :tableColumns="tableColumns"
          :operators="operators"
          :tableData="tableData" />
         <!-- <el-pagination
          class="pagination"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="studentForm.page"
          :page-sizes="[20,50,100,]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="tatalCount">
        </el-pagination> -->
      </div>
    </div>
  </div>
</template>

<script>
import myForm from "@/components/global/form/myForm"
import myTable from "@/components/global/table/myTable"
import {searchList,tableColumns,operators} from "@/data/studentMgr/config"
export default {
  name:"StedentMgr",
  components:{
    myForm,
    myTable
  },
  data(){
    return{
      studentForm:{
        name:"",
        sex:"",  //性别
        studentId:"", //学号
        level:"", //所在年级
        major:"",  //专业
        registYear:"", //入学时间
        domicilePlace:"", //户籍地
        page:1,
        size:20
      },
      currentPage:1,
      postList:[],
      tableData:[],
      tableColumns:[],
      operators:[],
      initList:[],
      tatalCount:0,
      loading:false
    }
  },
  mounted(){
    //初始化
    this.postList = searchList()
    this.tableColumns = tableColumns()
    this.operators = operators()
    this.initData()
    this.getPersonList()
  },
  methods:{
    async initData(){
      this.loading = true
      for(let i=0;i<601;i++){
        let sexRandomIndex = Math.round(Math.random())
        let levelRandomIndex = Math.round(Math.random()*3)
        let majorRandomIndex = Math.round(Math.random()*5)
        this.initList.push({
          name:"张三",sex:sexRandomIndex.toString(),studentId:"18098010086",level:levelRandomIndex.toString(),
          major:majorRandomIndex.toString(),registYear:"2018",domicilePlace:"成都"
        })
      }
      this.loading = false
      this.tableData = this.initList.slice(0,20)
      this.tatalCount = this.initList.length
    },
    getPersonList(){

    },
    handleCurrentChange(){

    },
    handleSizeChange(){}
  }
}
</script>

<style lang="less">
.stu-info{
  width:100%;
  height: 100%;
  box-sizing: border-box;
  padding: 16px;
  background-color: #F4F4F4;
  .stu-box{
    box-sizing: border-box;
    height: 100%;
    padding: 20px 20px 0 0;
    background-color: #fff;
    overflow: auto;
    .table-list{
      padding-left: 20px;
      .pagination{
        text-align: right;
        padding: 20px 0;
        width:100%;
        background-color: #fff;
      }
    }
  }
}
/*解决el-table表头右侧留白(需要el-table滚动条)*/
/* 1.最后一个表格border-right : none */
// .el-table .el-table__header-wrapper .has-gutter tr th:nth-last-child(2){
//   border-right: none !important;
// }
// /* 2.留白区域设置背景色 */
// .el-table th.gutter {
//   display: table-cell !important;
//   height: 34px;
//   background: rgb(247, 247, 247);
//   border-bottom: 1px solid #EBEEF5 !important;
//   position: absolute;
//   right: 0;
//   top: 0;
// }

/* 解决el-table表头右侧留白(不需要el-table滚动条)--start */
.el-table__body-wrapper {
  &::-webkit-scrollbar { // 整个滚动条
    width: 0; // 纵向滚动条的宽度
    background: rgba(213,215,220,0.3);
    border: none;
  }
  &::-webkit-scrollbar-track { // 滚动条轨道
    border: none;
  }
}
//隐藏table gutter列和内容区右侧的空白 start
.el-table th.gutter{
  display: none;
  width:0
}
.el-table colgroup col[name='gutter']{
  display: none;
  width: 0;
}
/* 解决el-table表头右侧留白(不需要el-table滚动条)--end */


/* 解决el-table右侧多出一列滚动条预留区域 */
.el-table__body{
  width:100% !important;
}
</style>



