<template>
  <div class="my-table">
    <el-table
      ref="myTable"
      :data="tableData"
      :max-height="maxHeight"
      border
      :header-cell-style="{backgroundColor:'#F7F7F7',color:'#A9A9A9'}"
      :cell-style="{fontSize:'12px'}"
      style="width: 100%">
      <el-table-column
        v-if="hasSelect"
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        v-for="(item,index) in tableColumns"
        :key="index"
        :prop="item.prop"
        :label="item.label"
        :min-width="item.minwidth"
        :fixed="item.fixed"
        show-overflow-tooltip
        align="center">
        <template slot-scope="scope">
            <span v-if="item.options">{{transfer(item.options,scope.row[item.prop])}}</span>
            <span v-else>{{scope.row[item.prop]}}</span>
        </template>
      </el-table-column>
      <el-table-column v-if="operators.length > 0" label="操作" width="90" align="center">
         <div v-for="(ele,i) in operators" :key="i" class="operator">{{ele.label}}</div>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name:"MyTable",
  props:{
    hasSelect:{
      type:Boolean,
      default:()=>false
    },
    tableData:{
      type:Array,
      default:()=>[]
    },
    tableColumns:{
      type:Array,
      default:()=>[]
    },
    operators:{
      type:Array,
      default:()=>[]
    },
    maxHeight:{
      type:String,
    }
  }
}
</script>

<style lang="less" scoped>
/* 修改el-table表头默认padding */
/deep/ .el-table .el-table__header-wrapper .el-table__header th{
  padding: 5px 0;
}

/deep/ .el-table .el-table__body-wrapper .el-table__row td{
  padding: 0;
}
.operator{
  color:#1979FE;
  font-size: 12px;
  text-align: center;
  cursor: pointer;
}
</style>



