import {SEX_OPTIONS,LEVEL_OPTIONS,MAJOR_OPTIONS} from "./constants"
//学生信息查询项
export const searchList = ()=>{
  return [
    {label:"姓名",prop:"name",type:"string"},
    {label:"性别",prop:"sex",type:"select",options:SEX_OPTIONS},
    {label:"学号",prop:"studentId",type:"number",length:15},
    {label:"年级",prop:"level",type:"select",options:LEVEL_OPTIONS},
    {label:"专业",prop:"major",type:"select",options:MAJOR_OPTIONS},
    {label:"入学时间",prop:"registYear",type:"date"},
    {label:"户籍地",prop:"domicilePlace",type:"string"},
  ]
}

//table
export const tableColumns = ()=>{
  return [
    {label:"姓名",prop:"name",minwidth:"100"},
    {label:"性别",prop:"sex",minwidth:"50",options:SEX_OPTIONS},
    {label:"学号",prop:"studentId",minwidth:"100"},
    {label:"年级",prop:"level",minwidth:"60",options:LEVEL_OPTIONS},
    {label:"专业",prop:"major",minwidth:"130",options:MAJOR_OPTIONS},
    {label:"入学时间",prop:"registYear",minwidth:"130"},
    {label:"户籍地",prop:"domicilePlace",minwidth:"130"},
  ]
}
//operators
export const operators = ()=>{
  return [
    {label:"个人信息"},
    {label:"其他"}
  ]
}



