<template>
  <div class="my-form">
    <el-form
      ref="postForm"
      :model="postForm"
      :rules="rules"
      size="small"
      :label-width="labelWidth">
      <el-row :gutter="20">
        <el-col v-for="(item,index) in postList"
          :key="index"
          :xs="24"
          :sm="12"
          :md="12"
          :lg="8"
          :xl="6">
          <el-form-item
            :prop="item.prop"
            :label="item.label">
              <template slot-scope>
                <!-- 字符串 -->
                <el-input v-if="item.type === 'string'"
                  v-model="postForm[item.prop]"
                  :placeholder="item.placeholder"
                  :clearable="Boolean(item.clearable) === false ? false : true"
                  :disabled="item.disabled"
                  @input="(val)=>checkValue(val,item)">
                </el-input>
                <!-- 数字 -->
                <el-input v-if="item.type === 'number'"
                  v-model="postForm[item.prop]"
                  :placeholder="item.placeholder"
                  :clearable="Boolean(item.clearable) === false ? false : true"
                  :disabled="item.disabled"
                  @input="(val)=>checkValue(val,item)">
                </el-input>
                <!-- 英文 -->
                <el-input v-if="item.type === 'letter'"
                  v-model="postForm[item.prop]"
                  :placeholder="item.placeholder"
                  :clearable="Boolean(item.clearable) === false ? false : true"
                  :disabled="item.disabled"
                  @input="(val)=>checkValue(val,item)">
                </el-input>
                <!-- 下拉框 -->
                <el-select v-if="item.type === 'select'"
                  v-model="postForm[item.prop]"
                  :placeholder="item.placeholder"
                  :multiple="Boolean(item.multiple) === false ? false : true"
                  :clearable="Boolean(item.clearable) === false ? false : true"
                  :multiple-limit="item.mulLimit"
                  :disabled="item.disabled"
                  style="width:100%">
                  <el-option v-for="(ele,i) in item.options"
                    :key="i"
                    :disabled="ele.disabled"
                    :label="ele.label"
                    :value="ele.value"></el-option>
                </el-select>
                <!-- 固定日期 -->
                <el-date-picker
                  v-if="item.type === 'date'"
                  v-model="postForm[item.prop]"
                  :disabled="item.disabled"
                  type="date"
                  :placeholder="item.placeholder"
                  style="width:100%">
                </el-date-picker>
                <!-- 日期范围 -->
                <el-date-picker
                  v-if="item.type === 'dateRange'"
                  v-model="postForm[item.prop]"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  :disabled="item.disabled"
                  type="daterange"
                  :placeholder="item.placeholder">
                </el-date-picker>
              </template>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- 搜索 -->
    <div class="search">
      <el-button type="primary" size="mini">搜索</el-button>
      <el-button size="mini">重置</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name:"Myform",
  props:{
    myForm:{
      type:Object,
      required:true,
      default:()=>{}
    },
    rules:{
      type:Object
    },
    labelWidth:{
      type:String,
      default:()=>'100px'
    },
    postList:{
      type:Array,
      required:true
    }
  },
  data(){
    return{
      postForm:this.myForm
    }
  }
}
</script>

<style lang="less" scoped>
.search{
  display: flex;
  justify-content: end;
  /deep/ .el-button{
    margin-right: 10px;
  }
}
</style>



